  async getStatelessWorkflow() {
    const currentHost = window.location.host;
    const appInfo = this.appInfoFromServer?.data?.application_details;
    const application_url = appInfo?.application_url;
    let baseUrl =
      location.hostname === 'localhost'
        ? `/api/k8s/helika/${appInfo?.application_url_prefix}`
        : `/api/k8s/helika/${appInfo?.application_url_prefix}`;

    const url = `${baseUrl}/workflows/stateless-workflow.js?platform_version=${this.platformVersion}`;
    console.log('URL -->', url);

    try {
      lastValueFrom(this.http.get(url, { observe: 'response' }))
        .then((res) => {
          console.log('Workflow file loaded');
        })
        .catch((err) => {
          console.log('Error loading workflow file');
        });
    } catch (error: any) {
      console.error('Error', error.message);
    }
  }

