import 'zone.js/dist/zone-node';

import { APP_BASE_HREF } from '@angular/common';
import { ngExpressEngine } from '@nguniversal/express-engine';
import * as express from 'express';
import * as cors from 'cors';
import { existsSync } from 'fs';
import { join } from 'path';

import { AppServerModule } from './bootstrap.server';
const compression = require('compression');

// The Express app is exported so that it can be used by serverless Functions.
export function app(): express.Express {
  console.log(`Starting up the express app for main-cube`);
  const server = express();
  const browserBundles = join(process.cwd(), 'dist/apps/main-cube/browser');
  server.use(compression({ level: 8 }));
  server.use(cors());
  const indexHtml = existsSync(join(browserBundles, 'index.original.html'))
    ? 'index.original.html'
    : 'index';

  // Our Universal express-engine (found @ https://github.com/angular/universal/tree/main/modules/express-engine)
  server.engine(
    'html',
    ngExpressEngine({
      bootstrap: AppServerModule,
    })
  );
  server.set('view engine', 'html');
  server.set('views', browserBundles);

  // Serve static files from /browser
  server.get(
    '*.*',
    (req, res, next) => {
      // console.log(
      //   'Requesting for main-cube static asset with file name => ',
      //   req.url
      // );
      const now = new Date().toLocaleString('en-US', {
        timeZone: 'Asia/Kolkata',
      });
      res.setHeader('x-fn7-fetched-at-IST', now);
      next(); // Continue to the next middleware or the response
    },
    express.static(browserBundles, {
      maxAge: '1y',
    })
  );

  // All regular routes use the Universal engine
  server.get('*', async (req, res) => {
    // keep it async to avoid blocking the server thread
    console.log(
      'Requesting for main-cube index.html * ',
      req.url,
      req.originalUrl,
      req.hostname,
      req.headers.host
    );
    console.log('server req host 1  => ', req.header('x-fn7-host'));
    let host_name = req.header('x-fn7-host') || 'helix.dev.app.fn7.io';
    console.log('server req host 2 => ', host_name);
    host_name =
      host_name && host_name.includes('localhost')
        ? host_name
        : 'helix.dev.app.fn7.io';
    const appInfoResp = await fetch(`https://${host_name}/api/info`);
    if (!appInfoResp.ok) {
      console.error(`Error fetching app info: ${appInfoResp.status}`);
      console.error(`Error fetching app info: ${host_name}`);
    }
    const appInfo = await appInfoResp.json();
    // console.log('App info => ', host_name, appInfo);
    const appInfoHeaders = appInfoResp.headers;
    const platformVersion = appInfoHeaders.get('PLATFORM-VERSION');
    console.log('platform version =>', platformVersion);
    // console.log('appinfo 2=> ',appInfo);
    res.setHeader('Cache-Control', 'no-cache');
    res.set('Document-Policy', 'js-profiling');
    const now = new Date().toLocaleString('en-US', {
      timeZone: 'Asia/Kolkata',
    });
    res.setHeader('Surrogate-Key', 'main-cube init');
    res.setHeader('x-fn7-fetched-at-IST', now);
    const hostUrl = new URL(`https://${host_name}`);
    res.render(indexHtml, {
      providers: [
        { provide: APP_BASE_HREF, useValue: req.baseUrl },
        { provide: 'APP_INFO', useValue: appInfo },
        { provide: 'PLATFORM-VERSION', useValue: platformVersion },
        { provide: 'HOST_NAME', useValue: hostUrl.hostname },
        { provide: 'HOST', useValue: hostUrl.host },
      ],
      req,
    });
  });

  return server;
}

function run(): void {
  const port = process.env['PORT'] || 4200;

  // Start up the Node server
  const server = app();
  server.listen(port, () => {
    console.log(
      `Node Express server for main-cube, listening on http://localhost:${port}`
    );
  });
}

run();

export * from './bootstrap.server';
